*{
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #1a1b20;
}

.menu{
  padding: 20px;
  display: flex;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #092953;
}

.menu h1{
  color: rgb(224, 66, 18);
  font-size: 20px;
  align-items: center;
}

.menu .links{
  margin-left: auto;
}

.menu a{
  margin-left: 16px;
  text-decoration: none;
  font-size: 15px;
}

.menu a:hover{
  color: rgb(43, 226, 43);
}

.home button{
  color: black;
  background-color: #ff0000;
  margin: 0%;
}

.home h1{
  color: #093e53;
  text-align: center;
}

.blog-preview{
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: #530909;
  justify-content: center;
}

.blog-preview:hover{
  box-shadow: 1px 3px 5px hsla(152, 83%, 21%, 0.1);
}

.blog-preview h2{
  font-size: 20px;
  color: #530909;
  display: flex;
  justify-content: left;
}
.display-blog h5{
  text-align: center;
}